<template>
  <div>
    <div class="loading" v-show="loading">
      <div class="loadingBox"></div>
    </div>

    <div class="main">
      <div class="box">
        <p class="mainText">{{ text }}</p>
        <p class="subtitle">{{ name }}</p>
        <button class="btn" @click="getQueto">Random quote</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "main",
  mounted() {
    this.getQueto();
    setTimeout(() => (this.loading = false), 1500);
  },
  data() {
    return {
      text: "",
      name: "",
      loading: true,
    };
  },
  methods: {
    async getQueto() {
      const res = await fetch("https://api.quotable.io/random");
      const data = await res.json();
      this.text = data.content;
      this.name = data.author;
    },
  },
};
</script>

